import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Link from '@material-ui/core/Link';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import NoSsr from '@material-ui/core/NoSsr';
// import ReactJson from 'react-json-view';
const ReactJson = dynamic(import('react-json-view'), { ssr: false });
import { Chip } from '@material-ui/core';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

const styles = theme => ({
    paper: {
        padding: theme.spacing(3, 2)
    },
    link: {
        margin: theme.spacing(1),
    },
});


function Jobs(props) {
    const { classes } = props;
    return(
        <Container maxWidth="md">
            <Paper className={classes.paper}>
                <Typography variant="h2" gutterBottom>Jobs</Typography>
                <Typography paragraph>
                    Product-Live Data Factory allows you to create <b>Jobs</b>. A Job allows you to create imports and exports of data stored in Product-Live or manipulate files and data that are stored outside of Product-Live.
                    <br></br>
                    These Jobs consist of <b>Tasks</b> that are chained together.
                    Tasks are elementary actions, for example: FTP Get (allows to get a file on a sFTP/FTP server), CSV to XML (allows to convert a CSV file to a XML file which is easier to manipulate)...
                    <br></br>
                    The full list of elementary Tasks can be found here: <Link href={'/data-factory/tasks'} target="_blank" rel="noopener">view all Tasks</Link>.
                </Typography>
                <Typography paragraph>
                    Jobs can be executed <u>manually</u> or <u>periodically</u>.
                </Typography>
                <Typography paragraph>
                    An example of creating a Job for daily import data from your ERP to Product-Live would be as follow:
                    <ol>
                        <li><b>FTP Get</b> Get the latest CSV file on a FTP server.</li>
                        <li><b>CSV to XML</b> Transform the CSV to a XML format.</li>
                        <li><b>Transform XLST</b> Transform the XML to the format expected by Product-Live</li>
                        <li><b>Import Items</b> Import products to Product-Live</li>
                    </ol>
                </Typography>
                <Typography paragraph>
                    In this Job there are 4 Tasks chained.
                </Typography>
                <Typography variant="h3" gutterBottom>Pipelines</Typography>
                <Typography paragraph>
                    Jobs are executed in <b>Pipelines</b>. A pipeline is a work stack of Jobs with rule: <u>first in first out</u>.
                    <br></br>
                    By default you have one Pipeline on your account, but you can purchase as many Pipelines as you want. For example you can need multiple Pipelines in these cases:
                    <ul>
                        <li>You want to execute Jobs in parallel.</li>
                        <li>You want that the Jobs of your Sandbox Space can not have slowdown effects on Jobs of your Production Space.</li>
                        <li>You need that a Job must be executed at a precise time.</li>
                    </ul>
                    If you have several Pipelines, then a job can be associated with one or multiple Pipelines. This allow you to reduce the cost of adding Pipelines and find the right number of Pipelines for your needs.
                </Typography>
                <Typography variant="h3" gutterBottom>Create and edit Jobs</Typography>
                <Typography paragraph>
                    To create these Jobs you can choose between creating them with the graphical user interface or with an advanced mode where you describe your Job in a JSON file. First go to:
                    <Breadcrumbs separator={<NavigateNextIcon fontSize="small" />} aria-label="breadcrumb">
                        <Typography color="primary">Product-Live Settings</Typography>
                        <Typography color="primary">Data Factory</Typography>
                        <Typography color="primary">Jobs</Typography>
                    </Breadcrumbs>
                </Typography>

                <Typography variant="h4" gutterBottom>The graphical user interface</Typography>
                <Typography paragraph>
                    The graphical user interface is a good way to start. This is simple, even a non technical user can use this interface.
                </Typography>
                <Typography paragraph>
                    
                </Typography>

                <Typography variant="h4" gutterBottom>The advanced edition in JSON</Typography>
                <Typography paragraph>
                    The advanced edition is prefered by the advanced users of Product-Live Data Factory. It allows to quickly duplicate or mass edit Jobs definition.
                    <br></br>
                    For this you can download the <Link href={''}>VS Code Extension</Link> that will help you a lot for creating a valid JSON description of your Job.
                </Typography>

                <Typography variant="h5" gutterBottom>Core structure of a Job</Typography>
                <NoSsr>
                    <ReactJson
                        style={{width: "100%", overflowY: "auto", fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif;'}}
                        theme="railscasts"
                        name={false}
                        src={
                            {
                                "key": "exportProductsXSLX",    
                                "displayName": {
                                    "_default": "Export products in Excel format",
                                    "fra": "Exporter les produits au format Excel"
                                },
                                "description": {
                                    "_default": "Export products in Excel format from a selection of products on the grid",
                                    "fra": "Exporte les produits au format Excel à partir d'une sélection de produits depuis la grille"
                                },
                                "status": "DRAFT",
                                "trigger": "MANUAL",
                                "periodicity": [
                                    {
                                        "type": "EVERY_WEEKS",
                                        "params": [
                                            {
                                                "day": "MONDAY",
                                                "hour": "08:00",
                                                "timezone": "EUROPE/BERLIN"
                                            },
                                            {
                                                "day": "TUESDAY",
                                                "hour": "09:00",
                                                "timezone": "EUROPE/BERLIN"
                                            }
                                        ]
                                    }                                    
                                ],
                                "labels": ["exports","web site"],
                                "userInputs": [],
                                "jobInputs": [],
                                "tasks": [],
                                "outputParameters": {},
                                "userOutputs": [],
                                "onError": ""
                            }                            
                        }
                        enableClipboard={false}
                        displayObjectSize={false}
                        displayDataTypes={false}
                        collapsed={1} />
                </NoSsr>
                <Table className={classes.table} size="small">
                    <TableHead>
                        <TableRow>
                            <TableCell>Property</TableCell>
                            <TableCell>Description</TableCell>
                            <TableCell>Status</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        <TableRow>
                            <TableCell>key</TableCell>
                            <TableCell>A unique key per Job. Best practice: use camel case.</TableCell>
                            <TableCell><Chip variant="outlined" size="small" label="required, max 75, unique" color="secondary" /></TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell>displayName</TableCell>
                            <TableCell>The name displayed to users. The default value is required</TableCell>
                            <TableCell><Chip variant="outlined" size="small" label="_default required, max 75" color="secondary" /></TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell>description</TableCell>
                            <TableCell>The descriptin displayed to users. If a value is set, then the _default lang is required.</TableCell>
                            <TableCell><Chip variant="outlined" size="small" label="optional" /></TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell>status</TableCell>
                            <TableCell>Define the status of the Job:<br></br>DRAFT | PRODUCTION | STAGING | ARCHIVED</TableCell>
                            <TableCell><Chip variant="outlined" size="small" label="required" color="secondary" /></TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell>trigger</TableCell>
                            <TableCell>Define how the Job is triggered:<br></br>MANUAL | PERIODIC | MANUAL_OR_PERIODIC</TableCell>
                            <TableCell><Chip variant="outlined" size="small" label="required" color="secondary" /></TableCell>
                        </TableRow>
                    </TableBody>
                </Table>
            </Paper>            
        </Container>
    )
}

Jobs.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Jobs);